
<template>
  <div class="side-nav">
    <div v-for="(title, index) in (Object.keys(data))" class="group-container" :key="index+'index'" >
      <p class="side-nav-title">{{title}}</p>
      <div class="side-nav-items" v-for="(nav,index) in data[title]" :key="index+'index'">
        <router-link :class="$route.name===nav.name ? 'active' : ''" v-if="nav.name" :to="{name: nav.name}">{{nav.desc}}</router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import navConf from '../nav.config.json'
  export default {
    data () {
      return {
        data: navConf
      }
    }
  }
</script>

<style lang="scss">
  .side-nav {
      text-align: left;
      padding: 0 1vw;
      .side-nav-title {
          font-size: 18px;
          color: blue;
      }
      .side-nav-items {
          line-height: 24px;
          color: #ccc;
          a {
            text-decoration: none;
            color: blue;
          }
          .active {
            color:#1bd1e9;
          }
      }
  }
</style>
